.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}
.header-bg{
    background-color: rgba(0,0,0,0.85);
    transition: 0.5s;
    

}
.nav-manu{
    display: flex;
}
.nav-manu li{
    padding: 0 1rem;
}
.nav-manu li a{
    font-size: 1.2rem;
    font-weight: 500;
}
.hamburger{
    display: none;
}
@media screen and (max-width: 1040px) {
    .nav-manu{
        justify-content: center;
        flex-direction: column;
        align-items: center ;
        width: 100%;
        height: 100vh;
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -1;
        transition:  0.5s;
   }
   .nav-manu.active{
    left: 0;
    background-color: black;
    opacity: 0.95;
   }
   .nav-manu li{
    padding: 1rem 0;
   }
  .nav-manu li a{
        font-size: 2rem;
   }
   .hamburger{
    display: inline;
}
}